<template>
  <div ref="app" class="app safe-areas">
    <Header v-if="isAuthenticated" />
    <router-view />

    <DynamicDialog />

    <div v-if="isDemoModalOpen && isAuthenticated" class="demo-modal">
      <i class="close-btn pi pi-times" @click="isDemoModalOpen = false" />
      <h2>Not Final Product</h2>
      <span>Content is subject to change and some is placeholder</span>
      <span>Styling can be changed as desired</span>
      <span>All connections involving the server have been cut for the demonstration</span>
      <span>Live data has been replaced by simulated random data</span>
      <span>On booking page, when asked for a card use 5248 4800 0021 0026 with a cvv of 111</span>
      <span>expiry date can be anything later than current date and postcode can by anything</span>
      <span>If asked for a verifcation code, use 123456</span>
      <span />
      <Button class="p-button-info" label="Close" @click="isDemoModalOpen = false" />
    </div>
  </div>
</template>

<script setup>
import Header from '@/components/core/Header.vue';

import { ref, reactive, computed, watch, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import { useHead } from '@vueuse/head';
import { useAppStore } from '@/stores/app';

const route = useRoute();

const isAuthenticated = ref(localStorage.getItem('ldnl-sitepass') === 'ldnl-chabadyouth');
const isDemoModalOpen = ref(true);

const {
  updateWeather,
  updateNews
} = useAppStore();

const app = ref();

const siteData = reactive({
  title: 'Little Desert Nature Lodge',
  canonical: 'http://localhost:8081/'
});

useHead({
  title: computed(() => siteData.title),
  meta: [
    {
      name: 'description',
      content: 'Escape to Little Desert Nature Lodge for a serene retreat in the heart of the Australian outback. Book now and experience tranquility.'
    }
  ],
  link: [
    {
      rel: 'canonical',
      href: computed(() => siteData.canonical)
    }
  ]
});

watch(() => route.path, (oldPath, newPath) => {
  if (oldPath !== newPath) {
    isAuthenticated.value = localStorage.getItem('ldnl-sitepass') === 'ldnl-chabadyouth';

    document.activeElement.blur();

    const routeName = route.name;
    siteData.title = 'Little Desert Nature Lodge';
    siteData.canonical = 'http://10.69.2.62:8081/';

    if (routeName === 'About the Lodge') {
      siteData.title += ' | About';
      siteData.canonical += 'about';
    } else if (routeName === 'Malleefowl') {
      siteData.title += ' | Malleefowl';
      siteData.canonical += 'about/malleefowl';
    } else if (routeName === 'History') {
      siteData.title += ' | History';
      siteData.canonical += 'about/history';
    } else if (routeName === 'Location') {
      siteData.title += ' | Location';
      siteData.canonical += 'about/location';
    } else if (routeName === 'Accommodation Home') {
      siteData.title += ' | Accommodation';
      siteData.canonical += 'accommodation';
    } else if (routeName === 'Booking') {
      siteData.title += ' | Booking';
      siteData.canonical += 'accommodation/booking';
    } else if (routeName === 'StandardEnsuite') {
      siteData.title += ' | Standard Ensuite';
      siteData.canonical += 'accommodation/standard-ensuite';
    } else if (routeName === 'DeluxeGarden') {
      siteData.title += ' | Deluxe Garden View';
      siteData.canonical += 'accommodation/deluxe-garden';
    } else if (routeName === 'Bunkroom') {
      siteData.title += ' | Bunkroom';
      siteData.canonical += 'accommodation/bunkroom';
    } else if (routeName === 'PoweredCamping') {
      siteData.title += ' | Powered Camping';
      siteData.canonical += 'accommodation/powered-camping';
    } else if (routeName === 'UnpoweredCamping') {
      siteData.title += ' | Unpowered Camping';
      siteData.canonical += 'accommodation/unpowered-camping';
    } else if (routeName === 'Explore Home') {
      siteData.title += ' | Explore';
      siteData.canonical += 'explore';
    } else if (routeName === 'Walking Trails') {
      siteData.title += ' | Trails';
      siteData.canonical += 'explore/trails';
    } else if (routeName === 'Flora') {
      siteData.title += ' | Flora';
      siteData.canonical += 'explore/flora';
    } else if (routeName === 'Wildlife') {
      siteData.title += ' | Wildlife';
      siteData.canonical += 'explore/wildlife';
    } else if (routeName === 'Stargazing') {
      siteData.title += ' | Stargazing';
      siteData.canonical += 'explore/stargazing';
    } else if (routeName === 'Bird Watching') {
      siteData.title += ' | Bird Watching';
      siteData.canonical += 'explore/bird-watching';
    } else if (routeName === 'Groups Home') {
      siteData.title += ' | Groups';
      siteData.canonical += 'groups';
    } else if (routeName === 'School') {
      siteData.title += ' | School';
      siteData.canonical += 'groups/school';
    } else if (routeName === 'Functions') {
      siteData.title += ' | Functions';
      siteData.canonical += 'groups/functions';
    } else if (routeName === 'Weddings') {
      siteData.title += ' | Weddings';
      siteData.canonical += 'groups/weddings';
    } else if (routeName === 'Contact') {
      siteData.title += ' | Contact Us';
      siteData.canonical += 'contact-us';
    } else if (routeName === 'Frequently Asked Questions') {
      siteData.title += ' | FAQ';
      siteData.canonical += 'frequently-asked-questions';
    } else if (routeName === 'News Feed' || routeName === 'News Post') {
      siteData.title += ' | News';
      siteData.canonical += 'news';
    }
  }
});

onMounted(() => {
  document.querySelector('meta[name="theme-color"]').setAttribute('content', '#000000');
  document.querySelector('meta[name="msapplication-TileColor"]').setAttribute('content', '#B0C4E8');

  updateWeather();
  updateNews();

  setInterval(() => {
    updateWeather();
  }, 15 * 60 * 1000);
});
</script>

