<script setup>
import { ref, reactive, watch, onBeforeMount } from 'vue';
import { useRoute } from 'vue-router';
import * as datefns from 'date-fns';
import { usePrimeVue } from 'primevue/config';
import { storeToRefs } from 'pinia';
import { useAppStore } from '@/stores/app';

const route = useRoute();
const primevue = usePrimeVue();

const appStore = useAppStore();
const { theme } = storeToRefs(appStore);

const hamburger = ref();
const hamburgerChecked = ref(false);

const navbar = reactive({
  about: {
    hover: 0,
    visible: false
  },
  accommodation: {
    hover: 0,
    visible: false
  },
  explore: {
    hover: 0,
    visible: false
  },
  groups: {
    hover: 0,
    visible: false
  },
  subnav: false
});

watch(() => route.path, (oldp, newp) => {
  if (oldp !== newp) {
    navbar.about.hover = 0;
    navbar.accommodation.hover = 0;
    navbar.explore.hover = 0;
    navbar.groups.hover = 0;
    navbar.subnav = false;

    setTimeout(() => {
      if (hamburger.value) {
        hamburger.value.checked = false;
      }

      if (navbar.about.hover === 0 && navbar.accommodation.hover === 0 && navbar.explore.hover === 0 && navbar.groups.hover === 0) {
        navbar.about.visible = false;
        navbar.accommodation.visible = false;
        navbar.explore.visible = false;
        navbar.groups.visible = false;
      }
    }, 1100);
  }
});

function toggleVisibility(navitem) {
  navbar.about.visible = navitem === 'about';
  navbar.accommodation.visible = navitem === 'accommodation';
  navbar.explore.visible = navitem === 'explore';
  navbar.groups.visible = navitem === 'groups';
}

function handleVisibility() {
  if (navbar.about.hover === 0 && navbar.accommodation.hover === 0 && navbar.explore.hover === 0 && navbar.groups.hover === 0) {
    navbar.subnav = false;

    // setTimeout(() => {
    //   if (navbar.about.hover === 0 && navbar.accommodation.hover === 0 && navbar.explore.hover === 0 && navbar.groups.hover === 0) {
    //     navbar.about.visible = false;
    //     navbar.accommodation.visible = false;
    //     navbar.explore.visible = false;
    //     navbar.groups.visible = false;
    //   }
    // }, 1100);
  }
}

function mouseenter(navitem) {
  navbar[navitem].hover += 1;
  toggleVisibility(navitem);
  navbar.subnav = true;
}

function mouseleave(navitem) {
  if (navitem === 'about') {
    navbar.about.hover -= 1;

    setTimeout(() => {
      if (navbar.about.hover <= 0) {
        navbar.about.hover = 0;
        handleVisibility();
      }
    }, 900);
  } else if (navitem === 'accommodation') {
    navbar.accommodation.hover -= 1;

    setTimeout(() => {
      if (navbar.accommodation.hover <= 0) {
        navbar.accommodation.hover = 0;
        handleVisibility();
      }
    }, 900);
  } else if (navitem === 'explore') {
    navbar.explore.hover -= 1;

    setTimeout(() => {
      if (navbar.explore.hover <= 0) {
        navbar.explore.hover = 0;
        handleVisibility();
      }
    }, 900);
  } else if (navitem === 'groups') {
    navbar.groups.hover -= 1;

    setTimeout(() => {
      if (navbar.groups.hover <= 0) {
        navbar.groups.hover = 0;
        handleVisibility();
      }
    }, 900);
  }
}

function toggleTheme() {
  if (theme.value === 'dark') {
    primevue.changeTheme('mdc-dark-indigo', 'mdc-light-indigo', 'theme-link', () => {
      document.documentElement.style.setProperty('--theme-hover', 'gray');
      document.documentElement.style.setProperty('--theme-image-icon', 'invert(100%)');
      theme.value = 'light';
    });
  } else {
    primevue.changeTheme('mdc-light-indigo', 'mdc-dark-indigo', 'theme-link', () => {
      document.documentElement.style.setProperty('--theme-hover', 'white');
      document.documentElement.style.setProperty('--theme-image-icon', 'none');
      theme.value = 'dark';
    });
  }
}

onBeforeMount(() => {
  if (window.matchMedia('(prefers-color-scheme: light)').matches) {
    toggleTheme();
  } else if (!window.matchMedia('(prefers-color-scheme: dark)').matches) {
    const hour = datefns.getHours(new Date());
    if (hour > 6 && hour < 20) {
      toggleTheme();
    }
  }

  window.matchMedia('(prefers-color-scheme: dark)')
    .addEventListener('change', ({ matches: isDark }) => {
      if (isDark && theme.value === 'light') {
        toggleTheme();
      } else if (!isDark && theme.value === 'dark') {
        toggleTheme();
      }
    });
});
</script>

