    border-radius: 50%;

    cursor: pointer;
    touch-action: manipulation;
    -webkit-tap-highlight-color: transparent;
    outline-offset: 5px;

    position: absolute;
    left: calc(var(--size) * 0.1);
    top: 0.25rem;
    z-index: 20;

    &>svg {
      inline-size: 100%;
      block-size: 100%;
      stroke-linecap: round;
    }

    .sun-and-moon {
      &> :is(.moon, .sun, .sun-beams) {
        transform-origin: center center;
