  @media screen and (max-width: 1186px) {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 1rem;
    padding-right: 7rem;

    nav {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;
      position: fixed;
      z-index: 18;
      top: 0;
      left: 0;
      padding: 3.5rem 0.5rem 0.5rem 0.5rem;
      background-color: var(--surface-section);
      overflow-y: scroll;
      -webkit-clip-path: circle(0px at 100vw 0px);
      clip-path: circle(0px at 100vw 0px);

      .nav-item,
      .nav-link {
        opacity: 0;
        color: var(--text-color) !important;

        &:hover {
          background-color: transparent;
          color: var(--theme-hover) !important;
        }

        &.sml {
          display: flex;
        }

        .pi {
          display: none;
        }

        @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
          transition: opacity .7s ease-in-out;
          $var: .1;

          @for $i from 1 through 27 {
            &:nth-child(#{$i}) {
              transition-delay: #{$var}s;
              $var: $var + .05;
            }
          }
        }
      }
    }

    .nav::-webkit-scrollbar {
      width: 20px;
    }

    .nav::-webkit-scrollbar-track {
      background-color: transparentize(black, 0.8);
      border-bottom-right-radius: 0.5rem;
    }

    .nav::-webkit-scrollbar-thumb {
      background-color: var(--surface-800);
      border-radius: 20px;
      border: 6px solid transparent;
      background-clip: content-box;

      &:hover {
        background-color: var(--surface-500);
      }
    }

    .subnav {
      display: none;
    }

    .nav-logo {
      display: none;
    }

    .theme {
      position: relative !important;
      --size: 2rem;
      // --icon-fill: var(--text-color);
      // --icon-fill-hover: var(--theme-hover);
    }

    .sml-logo {
      display: block;
      height: 3.75rem;

      .logo {
        height: 3.75rem;
        width: auto;
        aspect-ratio: 1 / 1;
        object-fit: contain;
      }
    }

    .hamburger-menu {
      --x-width: calc(var(--hamburger-height) * 1.4);

      display: flex;
      flex-direction: column;
      gap: var(--hamburger-gap);
      width: max-content;
      position: absolute;
      top: var(--hamburger-margin);
      right: var(--hamburger-margin);
      z-index: 21;
      cursor: pointer;
      touch-action: manipulation;

      &::before,
      &::after,
      input {
        content: "";
        width: var(--bar-width);
        height: var(--bar-height);
        // background-color: var(--text-color);
        background-color: var(--gray-200);
        border-radius: 0.3rem;
      }

      &:hover {

        &::before,
        &::after,
        input {
          background-color: white;
        }
      }

      &:has(input:focus-visible) {

        &::before,
        &::after {
          outline: 1px solid var(--surface-section);
          box-shadow: 0 0 0 2px var(--text-color);
        }

        &:has(input:checked) {
          &::after {
            translate: 0 calc(-1.2 * (var(--bar-height) + var(--hamburger-gap))) !important;
          }
        }

        input {
          width: calc(var(--bar-width) + 2px);
          height: calc(var(--bar-height) + 2px);
          border: 1px solid var(--surface-section);
          box-shadow: 0 0 0 1px var(--text-color);
          translate: -1px;
        }
      }

      input {
        appearance: none;
        padding: 0;
        margin: 0;
        outline: none;
        pointer-events: none;
      }
    }

    &.theme-light {
      nav {
        background: var(--surface-a);
      }

      @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {

        .sun,
        .sun-beams,
        .moon {
          transition: fill 0.5s ease-in-out;
          transition-delay: 0.5s;
        }

        .hamburger-menu {

          &:before,
          &:after,
          input {
            transition: background-color 0.5s ease-in-out;
            transition-delay: 0.5s;
          }
        }

        &:not(:has(input:checked)) {

          .sun,
          .sun-beams,
          .moon {
            transition-delay: 0;
          }

          .hamburger-menu {

            &:before,
            &:after,
            input {
              transition-delay: 0;
            }
          }
        }
      }

      &:has(input:checked) {
        .theme {
          --icon-fill: var(--text-color);
          --icon-fill-hover: var(--text-color);
        }

        .hamburger-menu {

          &:before,
          &:after,
          input {
            background-color: var(--text-color);
          }
        }
      }
    }

    &:has(input:checked) {

      .nav-item,
      .nav-link {
        opacity: 1;
      }

      @media screen and (prefers-reduced-motion: reduce),
      (update: slow),
      (update: none) {
        .hamburger-menu {
          &::before {
            translate: 0 calc(var(--bar-height) + var(--hamburger-gap));
            rotate: 45deg;
          }

          &::after {
            translate: 0 calc(-1 * (var(--bar-height) + var(--hamburger-gap)));
            rotate: -45deg;
          }

          input {
            opacity: 0;
          }
        }

        @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {

          .nav-item,
          .nav-link {
            $var: .5;

            @for $i from 1 through 9 {
              &:nth-child(#{$i}) {
                transition-delay: #{$var}s;
                $var: $var - .05;
              }
            }
          }
        }
      }

      nav {
        -webkit-clip-path: circle(calc(max(130vw, 130vh)) at 100vw 0px);
        clip-path: circle(calc(max(130vw, 130vh)) at 100vw 0px);
      }
    }

    @media screen and (prefers-reduced-motion: no-preference) and (update: fast) {
      .hamburger-menu {
        &::before {
          animation: hamburgerTop-rev var(--animation-timing);
        }

        &::after {
          animation: hamburgerBot-rev var(--animation-timing);
        }

        input {
          animation: hamburgerMid-rev var(--animation-timing);
        }
      }

      &:has(input:checked) {
        .hamburger-menu {
          &::before {
            animation: hamburgerTop var(--animation-timing);
          }

          &::after {
            animation: hamburgerBot var(--animation-timing);
          }

          input {
            animation: hamburgerMid var(--animation-timing);
          }
        }
      }

      nav {
        transition: all 1s ease-out;
      }
    }
  }
}

@mixin keyframe-reverse($name, $value) {
  @keyframes #{$name}-rev {
    @each $position, $change in $value {
      #{ 100 -$position}% {
        @each $prop, $val in $change {
          #{$prop}: #{$val};
        }
      }
    }
  }
}

@mixin keyframe-normal($name, $value) {
  @keyframes #{$name} {
    @each $position, $change in $value {
      #{$position}% {
        @each $prop, $val in $change {
          #{$prop}: #{$val};
        }
      }
    }
  }
}

@mixin keyframe-gen($name, $value, $genReverse) {
  @include keyframe-normal($name: $name, $value: $value);

  @if ($genReverse) {
    @include keyframe-reverse($name: $name, $value: $value)
  }
}

@include keyframe-gen($name: "themeSun",
  $value: (0: (transform: scale(1)),
    100: (transform: scale(1.75))),
  $genReverse: true) @include keyframe-gen($name: "themeSunBeams",
  $value: (0: (transform: rotateZ(0deg),
      opacity: 1),
    100: (transform: rotateZ(-25deg),
      opacity: 0)),
  $genReverse: true) @include keyframe-gen($name: "themeMoon",
  $value: (0: (transform: translateX(0)),
    100: (transform: translateX(-7px))),
  $genReverse: true) @include keyframe-gen($name: "themeMoonCX",
  $value: (0: (cx: 27),
    100: (cx: 17)),
  $genReverse: true) @include keyframe-gen($name : "hamburgerTop",
  $value : (50: (translate: 0 calc(var(--bar-height) + var(--hamburger-gap))),
    60: (translate: 0 calc(var(--bar-height) + var(--hamburger-gap)),
      rotate: none),
    100: (translate: 0 calc(var(--bar-height) + var(--hamburger-gap)),
      rotate: 45deg)),
  $genReverse : true);

@include keyframe-gen($name: "hamburgerMid",
  $value: (55: (opacity: 1),
    60: (opacity: 0),
    100: (opacity: 0)),
  $genReverse: true);

@include keyframe-gen($name : "hamburgerBot",
  $value : (50: (translate: 0 calc(-1 * (var(--bar-height) + var(--hamburger-gap)))),
    60: (translate: 0 calc(-1 * (var(--bar-height) + var(--hamburger-gap))),
      rotate: none),
    100: (translate: 0 calc(-1 * (var(--bar-height) + var(--hamburger-gap))),
      rotate: -45deg)),
  $genReverse : true);
</style>

