import { createApp } from 'vue';
import App from '@/App.vue';
import '@/registerServiceWorker';
import router from '@/router';
import store from '@/store';
import { createPinia } from 'pinia';
import { createHead } from '@vueuse/head';
import PrimeVue from 'primevue/config';
import PrimeVueComponents from '@/primevue';
import { VueReCaptcha } from 'vue-recaptcha-v3';
import { VueResponsiveComponents } from 'vue-responsive-components';
import VueSplide from '@splidejs/vue-splide';
import { animateFill } from 'tippy.js';
import { createTooltipPlugin } from '@/plugins/tooltip';
import ResizeDirective from '@/plugins/resize';

import '@/assets/weather-icons/sass/weather-icons.min.scss';
import 'material-design-icons-iconfont/dist/material-design-icons.css';
import '@splidejs/vue-splide/dist/css/themes/splide-default.min.css';
import 'animate.css/animate.min.css';

// Primevue
import 'primevue/resources/primevue.min.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';

// Tippy
import 'tippy.js/dist/backdrop.css';
import 'tippy.js/animations/shift-away.css';
import 'tippy.js/animations/perspective.css';
import 'tippy.js/themes/translucent.css';
import 'tippy.js/themes/light.css';

import '@/assets/theme.css';

const app = createApp(App)
.use(createPinia())
.use(createHead())
.use(store)
.use(router)
.use(PrimeVue)
.use(PrimeVueComponents)
.use(VueReCaptcha, { 
  siteKey: process.env.VUE_APP_RECAPTCHA_KEY as string,
  loaderOptions: {}
})
.use(VueResponsiveComponents)
.use(VueSplide)
.use(createTooltipPlugin({
    theme: 'translucent',
    animation: 'perspective',
    animateFill: true,
    plugins: [ animateFill ]
  }))
.directive('resize', ResizeDirective)
.mount('#app');

export default app;
