import { ref, computed, toRaw } from 'vue';
import { defineStore } from 'pinia';
import structuredClone from '@ungap/structured-clone';
import fetchPlus from '@/utilities/FetchPlus';
import getHost from '@/utilities/GetHost';

export const useAppStore = defineStore('app', () => {
  const theme = ref('dark');
  const timeOfDay = ref('day');
  const curWeather = ref({});
  const weatherUnit = ref('metric');
  const dangerRating = ref('Not Defined');
  const newsIDs = ref([]);

  const weather = computed(() => {
    const convertToF = (celsius) => {
      return celsius * 1.8 + 32;
    };

    if (Object.keys(curWeather.value).length === 0) {
      return {};
    }

    if (weatherUnit.value === 'metric') {
      return curWeather.value;
    }

    const output = structuredClone(toRaw(curWeather.value));
    output.main.temp = convertToF(output.main.temp);
    output.main.feels_like = convertToF(output.main.feels_like);
    output.main.temp_min = convertToF(output.main.temp_min);
    output.main.temp_max = convertToF(output.main.temp_max);
    return output;
  });

  function updateWeather() { // TODO: Randomized for off-site demonstration
    // fetchPlus(`${getHost()}/bookings/weather`, 15, {
    //   headers: {
    //     'Content-Type': 'application/json',
    //   },
    // }).then((res) => {
    //   timeOfDay.value = res.result.time_of_day;
    //   curWeather.value = res.result.weather;
    //   dangerRating.value = res.result.danger_rating;
    // });

    const dangerRatingRand = Math.random();
    if (dangerRatingRand > 0.8) {
      dangerRating.value = 'Catastrophic';
    } else if (dangerRatingRand > 0.6) {
      dangerRating.value = 'Extreme';
    } else if (dangerRatingRand > 0.4) {
      dangerRating.value = 'High';
    } else if (dangerRatingRand > 0.2) {
      dangerRating.value = 'Moderate';
    } else {
      dangerRating.value = 'No Rating';
    }

    const tempRand = Math.floor(Math.random() * (34 - 9) + 8);
    curWeather.value = {
      coord: {
        lon: 141.6679,
        lat: -36.454
      },
      weather: [
        {
          id: 801,
          main: 'Clouds',
          description: 'few clouds',
          icon: '02n'
        }
      ],
      base: 'stations',
      main: {
        temp: tempRand,
        feels_like: tempRand,
        temp_min: 8,
        temp_max: 34,
        pressure: 1016,
        humidity: 98,
        sea_level: 1016,
        grnd_level: 996
      },
      visibility: 10000,
      wind: {
        speed: 2.62,
        deg: 213,
        gust: 7.33
      },
      clouds: {
        all: 12
      },
      dt: 1723903786,
      sys: {
        type: 2,
        id: 2006715,
        country: 'AU',
        sunrise: 1723929157,
        sunset: 1723968126
      },
      timezone: 36000,
      id: 2155370,
      name: 'Nhill',
      cod: 200
    };

    const timeRand = Math.random();
    if (timeRand > 0.3) {
      timeOfDay.value = 'day';
    } else {
      timeOfDay.value = 'night';
    }
  }

  function toggleWeatherUnit() {
    if (weatherUnit.value === 'metric') {
      weatherUnit.value = 'imperial';
    } else {
      weatherUnit.value = 'metric';
    }
  }

  function updateNews() {
    // fetchPlus(`${getHost()}/data/news/list`, 15, {
    //   headers: {
    //     'Content-Type': 'application/json',
    //   },
    // }).then((response) => {
    //   newsIDs.value = response;
    // });

    newsIDs.value = ['ff30debe-b07f-443f-abd0-2aeb5a1ab883'];
  }

  return {
    theme,
    timeOfDay,
    weather,
    weatherUnit,
    dangerRating,
    newsIDs,
    toggleWeatherUnit,
    updateWeather,
    updateNews,
  };
});
